.HomeGrid {
  display: flex;
}

.UserProfileSidebar {
  background-color: #2f303a;
  height: 120vh;
  /* grid-column: 1/2; */
  width: 500px;
}
.profileImage {
  width: 140px;
  height: 140px;
}
.ppulse {
  display: none;

  color: #302b2b;
  font-size: 20px;
  font-weight: bold;
  padding-top: 30px;
  padding-left: 30px;
}
.priorityPulse {
  font-size: 30px;
  padding-left: 65px;
  padding-top: 30px;
  padding-right: 30px;
}
.profile {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 80vh;
}
.profileDetails {
  display: flex;
  margin-top: 50px;
}
.profileDetail {
  margin: 0 10px;
}
.profileDetail p {
  margin-top: 10px;
}

@media screen and (max-width: 815px) {
  .priorityPulse {
    font-size: 20px;
    padding-right: 20px;
  }
  .profile p {
    font-size: 14px;
  }
  .profileDetail p {
    margin-top: 10px;
  }
  .profileDetail:nth-child(1),
  .profileDetail:nth-child(2) {
    margin-top: 10px;
  }
  .UserProfileSidebar {
    min-height: 100vh;
  }
}
@media screen and (max-width: 800px) {
  .profileDetail:nth-child(3) {
    margin-top: 10px;
  }
}
@media screen and (max-width: 640px) {
  .HomeGrid {
    display: flex;
    flex-direction: column;
    width: 100vw;
  }
  .ppulse {
    display: flex;
  }
  .UserProfileSidebar {
    width: 100vw;
    display: none;
  }
}
