.HospitalDetail {
  min-width: 320px;
}
.HospitalDetail p {
  color: #2f303a;
}
.hospital {
  font-size: 25px;
  font-weight: bold;

  margin-top: 50px;
}
.hdf {
  display: flex;
}
.sendLocationBtn {
  width: 7vw;
  margin-left: 50px;
  padding: 0 10px;
  background-color: #767e86;
  color: #fff;
  height: 40px;
  font-size: 14px;
  margin-top: 40px;
}
.hospitalAddress {
  font-size: 13px;
  color: rgba(0, 0, 0, 0.6);
  font-weight: normal;
}
.moreAboutHospitalDetails {
  /* display: ; */
  margin-top: 10px;
  overflow-x: auto;
}
.mahha {
  font-size: 18px;
  margin: 5px;
}
.moreAboutHospital {
  display: flex;
  flex-direction: row;
  font-size: 18px;
  margin: 0 10px;
  color: rgba(0, 0, 0, 0.6);
}
.moreAboutHospital p {
  margin-top: 10px;
}
.phoneicon {
  width: 15px;
  height: 15px;
}
@media screen and (max-width: 768px) {
  .sendLocationBtn {
    width: 10vw;
  }
}
@media screen and (max-width: 640px) {
  .HospitalDetail {
    margin-bottom: 20px;
    margin-left: 100px;
    width: 70vw;
  }
  .hospital {
    margin-top: 10px;
  }
  .hdf {
    flex-direction: column;
    margin-right: 50px;
  }
  .sendLocationBtn {
    margin-top: 0;
    width: 30vw;
    margin-left: 0px;
  }
  .hospitalAddress {
    font-size: 18px;
  }
  .mahha {
    font-size: 18px;
  }
  .moreAboutHospital {
    font-size: 20px;
  }
}
