.footer {
  min-height: 100px;
  background-color: #302b2b;
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 14px 6vw;
}
.prioritypulseFooter {
  font-size: 28px;
  font-weight: bold;
  /* padding: 14px 6vw; */
  color: #fdfdfd;
}
.copyRights {
  font-size: 16px;
  /* padding: 10px 6vw; */
  padding-top: 10px;
}
.ppgmail {
  color: #edf2f7;
  font-size: 16px;
}
.socialMediaList {
  display: flex;
  margin-top: 15px;
  cursor: pointer;
}
.socialMediaList li:hover {
  transform: scale(1.2);
}
.socialMediaList li {
  margin-right: 40px;
  transition: 0.3s ease;
}
.socialmediaicon {
  width: 25px;
  height: auto;
}
@media screen and (max-width: 540px) {
  .footer {
    flex-direction: column;
  }
  .footer-right-wrapper {
    margin-top: 40px;
  }
}
