@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  display: flex;
  flex-direction: column;
}
body {
  font-family: "Roboto", sans-serif;
  height: 100%;
  overflow-x: hidden;
}
li {
  list-style: none;
}
.NavbarGrid {
  position: absolute;
  right: 0;
}
p {
  color: #fdfdfd;
}
input,
button {
  border: none;
  outline: none;
}

.Navbar {
  width: 100vw;
}
.navbarList {
  display: flex;
  justify-content: flex-end;
  margin-right: 70px;
}
.navbarListItem {
  margin-right: 25px;
  margin-top: 30px;
}
.trackambulanceLink,
.loginLink,
.aboutusLink,
.logoutLink,
.registerLink {
  text-decoration: none;
  color: #2f303a;
  font-size: 16px;
  font-weight: bold;
  transition: all 0.3s ease;
  /* margin-right: 10px; */
}
/* .navbarListItem:hover {
  color: white;
  background: rgba(47, 48, 58, 0.8);
} */
.trackambulanceLink:hover,
.loginLink:hover,
.aboutusLink:hover,
.logoutLink:hover,
.registerLink:hover {
  color: white;
  background: #767e86;
  padding: 5px 10px;
}
.activeNavLink {
  color: white;
  background: rgba(47, 48, 58, 0.8);
  padding: 5px 10px;
}
.burger {
  border: 1px solid black;
  padding: 4px 8px;
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 25px;
  background-color: #f6f7f8;
  cursor: pointer;
  position: absolute;
  right: 0;
  transform: translateY(25px);
  display: none;
}
@media screen and (max-width: 940px) {
  .burger {
    position: fixed;
    display: flex;
    right: 20px;
  }
  .navbarList {
    display: none;
    opacity: 0;
    transition: all 0.5s ease;
  }
  .activeNavLink {
    font-size: 15px;
  }
  .navbarList.active {
    display: flex;
    flex-direction: column;
    opacity: 1;
    position: fixed;
    right: 30px;
    margin-top: 60px;
    background-color: #f6f7f8;
    z-index: 10;
    padding: 20px 40px;
    transition: all 0.5s ease;
  }
  .navbarListItem {
    font-size: 20px;
  }
  .trackambulanceLink:hover,
  .loginLink:hover,
  .aboutusLink:hover,
  .logoutLink:hover,
  .registerLink:hover {
    color: #2f303a;
    padding: 0;
  }
}

