.loginGrid {
  display: flex;
  overflow-x: hidden;
}
.LoginSidebar {
  background-color: #2f303a;
  min-height: 100vh;
}
.adGrid {
  margin-left: 60px;
  height: auto;
  padding-top: 40px;
  justify-self: center;
  align-self: center;
}
.priorityPulse {
  font-size: 30px;
  font-weight: 600;
}
.loginFormWrapper {
  margin-top: 100px;
  padding: 0 40px;
  max-width: 500px;
}

@media screen and (max-width: 768px) {
  .loginGrid {
    flex-direction: column;
    /* overflow-x: hidden; */
  }
  .adGrid {
    padding: 0 1px;
    margin-left: 10px;
  }
  .LoginSidebar {
    min-height: auto;
    max-height: auto;
  }
  .priorityPulse {
    padding-top: 20px;
    margin-left: -20px;
    font-size: 25px;
  }
  .loginFormWrapper {
    width: 90%;
    margin: 80px auto;
    padding: 0 0 40px 0;
  }
}

@media screen and (max-width: 360px) {
  .loginGrid {
    overflow-x: hidden;
  }
  .firebaseui-recaptcha-container > div > div {
    transform: scale(0.9);
    -webkit-transform: scale(0.9);
    transform-origin: 0 0;
    -webkit-transform-origin: 0 0;
  }
}

@media screen and (max-width: 320px) {
  .loginGrid {
    overflow-x: hidden;
    margin-left: 10px;
  }
  .firebaseui-recaptcha-container > div > div {
    transform: scale(0.9);
    -webkit-transform: scale(0.8);
    transform-origin: 0 0;
    -webkit-transform-origin: 0 0;
  }
}

.Statscontainer {
  width: 100%;
  color: #000;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1rem;
}

.heading {
  text-align: start;
  font-size: clamp(1.5rem, 5vw, 2rem);
  font-weight: 600;
  margin-bottom: 1rem;
  padding: 0 2rem;
}

.Wrapper {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
}

.StatsBox {
  padding: 2rem;
}
.Icon {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.Title {
  font-size: clamp(1.25rem, 2.5vw, 1.1rem);
  margin-bottom: 0.5rem;
  font-weight: 500;
}
.Desc {
  font-size: 13px;
}
@media screen and (max-width: 768px) {
  .Wrapper {
    grid-template-columns: 1fr;
  }
  .Statscontainer {
    padding: 30px;
    margin-top: 12rem;
  }
}
@media screen and (max-width: 500px) {
  .Wrapper {
    grid-template-columns: 1fr;
  }
  .StatsBox {
    padding: 1rem;
  }
}

