  padding: 0 2rem;
}
.aboutuscontainer p {
  text-align: start;
  margin-bottom: 1rem;
  font-size: 17px;
  font-weight: 200;
  padding: 0 2rem;
  color: #3b3b3b;
}
.contentwrapper {
  display: grid;
  grid-template-columns: 1.75fr 1fr;
}
.coloum1 {
  display: grid;

  grid-gap: 10px;
}
.coloum2 {
  display: grid;

  grid-gap: 10px;
}
.aImages {
  max-width: 400px;
  margin-top: -8rem;
}
@media screen and (max-width: 768px) {
  .contentwrapper {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 0.75fr;
