}
.uhowData {
  margin-top: 1rem;
  grid-gap: 10px;
}
@media screen and (max-width: 768px) {
  .howcontainer {
    grid-template-columns: 1fr;
    padding: 0 10px;
  }
}

.coln1 {
  display: grid;
  grid-template-rows: 0.3fr 1fr;
}

.coln2 {
  display: grid;
  justify-content: center;
}

.slider {
  margin-top: 5rem;
  overflow: hidden;
  position: relative;
  width: 90%;
}
.images {
  display: flex;
  width: 100%;
}
.images img {
  transition: all 0.15s ease;
  width: 100%;
}
.images input {
  display: none;
}
.dots {
  display: flex;
  justify-content: center;
  margin: 5px;
}
.dots label {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  border: solid #222222 3px;
  cursor: pointer;
  transition: all 0.15s ease;
  margin: 5px;
}
.dots label:hover {
  background: #3b3b3b;
}

.m1 {
  position: relative;
  max-width: 500px;
  transition: 2s;
}
#img1:checked ~ .first {
  margin-left: 0;
}
#img2:checked ~ .first {
  margin-left: -100%;
}
#img3:checked ~ .first {
  margin-left: -200%;
}
#img4:checked ~ .first {
  margin-left: -300%;
}

.coln2 h4 {
  color: #3498db;
}
@media screen and (max-width: 768px) {
  .coln2 {
    grid-template-columns: 1fr;
    padding-left: 30px;
  }
}
.col1inside {
  padding-left: 2rem;
  margin-top: -1rem;
  line-height: 30px;
}

.user2pic {
  position: relative;
  max-width: 500px;
}
@media screen and (max-width: 768px) {
  .user1pic {
    position: relative;
    max-width: 300px;
  }
  .user2pic {
    position: relative;
    max-width: 300px;
  }
  .col1inside {
    padding-left: 2rem;
    padding-right: 1rem;
    margin-top: -6rem;
    line-height: 30px;
  }
}

.Desch {
  color: #3b3b3b;
  font-weight: 300;
}
.Titleh {
  font-size: 22px;
}

