tr:nth-child(odd) {
  background-color: #edf2f7;
}
.noRides {
  font-size: 20px;
  min-width: 32vw;
}
.pastrideTable {
  border-collapse: collapse;
  width: 100%;
}
.pastrideDetails {
  margin-top: 10px;
}
.pastrideDetails td {
  padding: 10px;
  height: 40px;
  padding-right: 50px;
  font-size: 20px;
  cursor: pointer;
  white-space: nowrap;
}

@media screen and (max-width: 1200px) {
  .pastrideTable td {
    width: 60%;
    padding-right: 10px;
  }
}

@media screen and (max-width: 920px) {
  .pastrideTable {
    width: 60vw;
    margin: 0 30px;
  }
  .pastrideDetails td {
    padding-right: 20px;
    text-align: center;
  }
}
@media screen and (max-width: 640px) {
  .pastrideTable {
    margin: 0;
  }
  .pastrideDetails td {
    width: 100vw;
    font-size: 18px;
    padding-right: 5px;
  }
}
@media screen and (max-width: 320px) {
  .pastrideDetails td {
    font-size: 15px;
  }
}
