.presentrideTable {
  border-collapse: collapse;
  width: 100%;
}
.presentRideDetails {
  margin-top: 10px;
}
.presentRideDetails td {
  padding: 10px;
  height: 40px;
  padding-right: 50px;
  font-size: 20px;
  white-space: nowrap;
  cursor: pointer;
}
@media screen and (max-width: 1200px) {
  .presentrideTable td {
    width: 60%;
    padding-right: 10px;
    font-size: 20px;
  }
}
@media screen and (max-width: 920px) {
  .presentrideTable {
    width: 60vw;
    margin: 0 30px;
  }
  .presentRideDetails td {
    padding-right: 20px;
    text-align: center;
  }
}
@media screen and (max-width: 640px) {
  .presentrideTable {
    margin: 0;
  }
  .presentrideDetails td {
    width: 100vw;
    font-size: 18px;
    padding-right: 5px;
  }
}

@media screen and (max-width: 320px) {
  .presentRideDetails td {
    font-size: 15px;
  }
}
