@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.App {
  display: flex;
  flex-direction: column;
}
body {
  font-family: "Roboto", sans-serif;
  height: 100%;
  overflow-x: hidden;
}
li {
  list-style: none;
}
.NavbarGrid {
  position: absolute;
  right: 0;
}
p {
  color: #fdfdfd;
}
input,
button {
  border: none;
  outline: none;
}
