.TrackAmbulance {
  overflow-x: hidden;
}
.trackpriorityPulse {
  color: #302b2b;
  font-size: 28px;
  font-weight: bold;
  padding-top: 30px;
  padding-left: 75px;
}
.trackAmbulanceGrid {
  display: flex;
  margin: 40px 80px;
}
.trackMap {
  display: flex;
  justify-content: center;
  margin-left: 50px;
  width: 100%;
}
@media screen and (max-width: 1500px) {
  .trackAmbulanceGrid {
    margin-left: 20px;
  }
  .trackMap {
    padding-right: 20px;
    justify-content: center;
    margin-left: 20px;
  }
}
@media screen and (max-width: 1320px) {
  .trackAmbulanceGrid {
    margin: 40px 20px;
  }
  .trackMap {
    justify-content: flex-start;
    margin-left: 20px;
  }
}
@media screen and (max-width: 920px) {
  .trackAmbulanceGrid {
    flex-direction: column;
    align-items: center;
  }
  .trackMap {
    justify-content: center;
    width: 100vw;
    margin-top: 20px;
  }
}

@media screen and (max-width: 640px) {
  .trackpriorityPulse {
    padding-left: 30px;
    font-size: 20px;
  }
  .trackAmbulanceGrid {
    display: flex;
    flex-direction: column;
    margin: 40px 20px;
    align-items: flex-start;
  }
  .trackMap {
    margin-left: -10px;
    margin-top: 20px;
  }
}
