.SearchBar {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-right: 20px;
}
.SearchBar input {
  width: 39vw;
  background-color: #edf2f7;
  height: 40px;
  text-align: center;
  color: rgba(0, 0, 0, 0.5);
}
.myLocationBtn {
  width: 10vw;
  margin-left: 10px;
  background-color: #767e86;
  color: #fff;
  height: 40px;
  font-size: 18px;
}
@media screen and (min-width: 1300px) {
  .SearchBar input {
    width: 45vw;
    margin-left: 70px;
  }
}

@media screen and (max-width: 1200px) {
  .SearchBar input {
    width: 40vw;
  }
  .SearchBar {
    justify-content: start;
    margin-right: 20px;
  }
  .myLocationBtn {
    width: 15vw;
    font-size: 15px;
  }
}

@media screen and (max-width: 640px) {
  .SearchBar {
    margin-right: 20px;
  }
  .SearchBar input {
    width: 70vw;
  }
  .myLocationBtn {
    width: 22vw;
    font-size: 13px;
    padding: 5px;
  }
}
