.HospitalNames {
  margin-top: 30px;
}
.hospitalNamesTitle {
  font-size: 20px;
  font-weight: bold;
  color: #2f303a;
}
.hospitalNamesList {
  margin-top: 10px;
  width: 300px;
  min-height: 0;
  max-height: 170px;
  overflow-y: auto;
}
.hospitalName {
  min-height: 30px;
  font-size: 16px;
  color: #2f303a;
  padding: 5px 10px;
  cursor: pointer;
}
.hospitalName:nth-child(even) {
  background-color: #edf2f7;
}
@media screen and (max-width: 1200px) {
  .HospitalNames {
    /* margin-right: 60px; */
    align-self: start;
    /* margin-left: -20px; */
  }
}
@media screen and (max-width: 640px) {
  .HospitalNames {
    margin-top: 20px;
    margin-bottom: 40px;
    /* margin-right: 20px; */
    margin-left: 60px;
  }
  .hospitalNamesList {
    width: 70vw;
    min-height: 0;
  }
  .hospitalNamesTitle {
    font-size: 23px;
  }
  .hospitalName {
    font-size: 20px;
  }
}
