.TotalDetail {
  width: 300px;
  margin: 20px;
}
.totalDetailTitle {
  color: #575c92;
  font-size: 22px;
  font-weight: bold;
}
.totalDetailName {
  font-size: 23px;
  font-weight: bold;
  color: #2f303a;
}
.totalDetailContactWrapper,
.totalDetailCaseOrAddressWrapper {
  display: flex;
  margin-top: 10px;
}
.totalDetailContactWrapper p {
  color: rgba(0, 0, 0, 0.6);
  font-size: 18px;
}
.totalDetailCaseOrAddressWrapper p {
  color: rgba(0, 0, 0, 0.6);
  font-size: 18px;
}
.totalDetailContact,
.totalDetailAddress {
  padding-right: 20px;
}
.totalDetailContactNo,
.totalDetailAddressOrCase {
  padding-left: 20px;
}
.totalDetailContactNo {
  overflow-x: auto;
}
@media screen and (max-width: 640px) {
  .TotalDetail {
    width: 70vw;
  }
}
