.SideBar {
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 50vw;
  margin-left: 120px;
  margin-right: 100px;
  margin-top: 90px;
}
.hospitalDetailsTotal {
  width: 52vw;
  display: flex;
  justify-content: space-between;
}
@media screen and (min-width: 1410px) {
  .SideBar {
    position: absolute;
    right: 100px;
  }
}
@media screen and (min-width: 1300px) {
  .SideBar {
    margin-left: 50px;
  }
}
@media screen and (max-width: 1200px) {
  .SideBar {
    height: 100%;
    margin-left: 20px;
  }
  .hospitalDetailsTotal {
    /* flex-direction: column;
    align-items: center;
    margin-top: 20px;
    margin-left: 100px; */
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }
}
@media screen and (max-width: 768px) {
  .SideBar {
    /* margin-left: 20px; */
    margin-right: 50px;
  }
}
@media screen and (max-width: 640px) {
  .SideBar {
    margin-left: 10px;
    height: 100%;
    margin-top: 40px;
  }
  .hospitalDetailsTotal {
    margin: auto;
    margin-left: 20px;
  }
}
